const routers = [{
	path: '/index',
	component: (resolve) => {
		require(["./views/index.vue"], resolve);
	}
}, {
	path: '/lykjh',
	component: (resolve) => {
		require(["./views/calendar.vue"], resolve);
	}
}, {
	path: '*',
	redirect: '/index'
}];
export default routers;


