"use strict"

/** 获取我的项目状态 我的参赛项目
 *  @param {status}  getXmStatusStr
 */
exports.getXmStatusStr = xmStatu => {
	let xmStr = "";
	let numstatu=parseInt(xmStatu);
	switch(numstatu) {
		case 0:
			xmStr = "未提交";
			break;
		case 1:
			xmStr = "已提交";
			break;
		case 2:
			xmStr = "已通过";
			break;
		case 3:
			xmStr = "未通过";
			break;
		case 4:
			xmStr = "已展示";
			break;
		case 5:
			xmStr = "已下架";
			break;
	}
	return xmStr;
}


