import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './components/app.vue';
import Routers from './router';
import filters from './filters/filters';
import Env from '../config/env';
import FastClick from 'fastclick';

Vue.use(VueRouter);
//实例化Vue的filter
Object.keys(filters).forEach(key => {
	Vue.filter(key, filters[key])
});
// 并且配置路由规则
const router = new VueRouter({
	mode: 'hash',
	base: __dirname,
	routes: Routers
})
router.beforeEach((to, from, next) => {
	window.scrollTo(0, 0);
	FastClick.attach(document.body);
	if(to.query.mi) {
		localStorage.setItem("memberId", to.query.mi);
	}
	if(localStorage.memberId) {
		next();
	} else {
		if(Env == 'pre') {
			window.location.href = 'http://h5pre.5dou.cn/login_html/dl.html?callback={' + location.href + '}';
		} else if(Env == 'test' || Env == 'development') {
			window.location.href = 'http://h5test.5dou.cn/login_html/dl.html?callback={' + location.href + '}';
		} else if(Env == 'production') {
			window.location.href = 'http://h5.5dou.cn/login_html/dl.html?callback={' + location.href + '}';
		}
		next();
	}
});

router.afterEach(() => {

});
const app = new Vue({
	router: router,
	render: h => h(App)
}).$mount('#app')


